body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.navText {
  line-height: 65px;
  display: inline;
  float: right;
  margin-right: 4vw;
}
.navBar {
  border: 1px solid grey;
  -webkit-box-shadow: -1px 10px 5px grey;
          box-shadow: -1px 10px 5px grey;
}
.campusList{
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.campus{
  text-align: center;
  font-size: 34px;
  font-weight: 500;
  line-height: 150px;
  margin: 20px;
  width: 250px;
  height: 150px;
  border: 1px solid #000;
}
.cohortList{
  width: 95vw;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.cohort{
  text-align: center;
  font-size: 34px;
  font-weight: 500;
  line-height: 150px;
  margin: 20px;
  width: 250px;
  height: 150px;
  border: 1px solid #000;
}
.studentRowContainer{
  padding: 0px;
  margin: 0px;
  margin-top: 15px;
}
.studentRow{
  -webkit-box-shadow: 0px 3px 5px lightgrey;
          box-shadow: 0px 3px 5px lightgrey;
  width: 80vw;
  border: 1px solid #000;
  height: 50px;
  list-style: none;
  line-height: 50px;
  margin-left: 10vw;
  border-radius: 15px;
  text-align: center;
  margin-bottom: 10px;
}
.dataPoint{
  width: 200px;
  display: inline;
  padding-right: 20px;
}
.chartContainer{
  margin-top: 25px;
  margin-left: 25px;
  border: 1px solid #000;
  -webkit-box-shadow: -1px 10px 5px grey;
          box-shadow: -1px 10px 5px grey;
  height: 370px !important;
  width: 500px !important;
}
.indicator{
  border-radius: 50%;
  margin-top: 11px;
  margin-left: 7px;
  height: 25px;
  width: 25px;
  float: left;
}



